.left-menu {
  position: fixed;
  float: left;
  left: 0;
  top: 0;
  width: 70px;
  height: 100vh;
  background-color: #ffffff;
  border-right: 1px solid #f1f1f1;
  z-index: 9;
  -webkit-transition: .5s linear all;
  transition: .5s linear all;
}

.left-menu.active {
  width: 200px;
  -webkit-transition: .5s linear all;
  transition: .5s linear all;
}

@-webkit-keyframes onLeftMenu {
  from {
    width: 70px;
  }
  to {
    width: 200px;
  }
}

@keyframes onLeftMenu {
  from {
    width: 70px;
  }
  to {
    width: 200px;
  }
}

