  }
} */

.left-menu .left-menu-buttons {
  position: absolute;
  float: left;
  width: 100%;
  height: auto;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.left-menu .left-menu-buttons a {
  position: relative;
  float: left;
  -webkit-transition: 0.3s linear all;
  transition: 0.3s linear all;
  color: transparent;
  line-height: 23px;
  font-size: 0;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  z-index: 999;
}

.left-menu .left-menu-buttons a.active,
.left-menu .left-menu-buttons a:hover 
{
  box-shadow: 0px 5px 60px 0 rgba(0, 0, 0, 0.15);
  z-index: 9999;
}

.left-menu .left-menu-buttons a img {
  width: 25px;
  height: auto;
  position: relative;
  float: left;
  -webkit-transition: 0.3s linear all;
  transition: 0.3s linear all;
  color: #003077;
}

.left-menu.active .left-menu-buttons a img {
  margin-right: 30px;
}

.left-menu.active .left-menu-buttons a {
  line-height: 23px;
  font-size: 15px;
  color: #003077;
}

.toggleLeftMenu {
  position: absolute;
  left: 50px;
  bottom: 50px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #f1f1f1;
  background-color: #003077;
  box-shadow: 0px 2px 47px -22px rgba(0,0,0,1);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999999;
  -webkit-transition: .5s linear all;
  transition: .5s linear all;
}

.toggleLeftMenu.active
{
  left: 180px;
}

.toggleLeftMenu img
{
  width: 15px;
  height: 15px;
  -webkit-transition: .3s linear all;
  transition: .3s linear all;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.toggleLeftMenu.active img
{
  -webkit-transform: translate(-50%, -50%) rotate(-180deg);
          transform: translate(-50%, -50%) rotate(-180deg);
}
.toggleLeftMenuMobile
{
  display: none;
}

