.content {
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  /* background-color: black; */
}

.first-chart {
  position: relative;
  float: left;
  width: calc(70% - 10px);
  margin-right: 10px;
  height: calc(50% - 20px);
  margin-bottom: 20px;
  border: solid 1px #f1f1f1;
  background-color: #ffffff;
}

.chart {
  -webkit-animation: showCharts 1.7s;
          animation: showCharts 1.7s;
  border: 1px solid #eeeeee;
}

.chart.hide {
  -webkit-animation: hideCharts 1.7s;
          animation: hideCharts 1.7s;
}
@-webkit-keyframes hideCharts {
  from {
    opacity: 1;
  }
  to {
    -webkit-transition: 0.5s linear all;
    transition: 0.5s linear all;
    opacity: 0;
  }
}
@keyframes hideCharts {
  from {
    opacity: 1;
  }
  to {
    -webkit-transition: 0.5s linear all;
    transition: 0.5s linear all;
    opacity: 0;
  }
}

@-webkit-keyframes showCharts {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes showCharts {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

