/* POPUP */

.popup
{
  display: none;
}
.popup.active
{
  position: absolute;
  float: left;
  width: 100%;
  height: 100vh;
  background-color: rgba(17, 17, 17, .3);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999999;
}
.popup.active .popup-inside
{
  position: relative;
  float: left;
  max-width: 600px;
  height: auto;
  background-color: #fff;
  display: flex;
  max-height: calc(100% - 96px);
  flex-direction: column;
  border-radius: 5px;
}
.popup.active .popup-inside .content-popup
{
  flex: 0 0 auto;
  margin: 0;
  padding: 16px 24px;
}
.popup.active .popup-inside span
{
  position: relative;
  float: left;
  width: 100%;
  font-size: 1.25rem;
  display: flex;
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.0075em;
}
.popup.active .popup-inside span:nth-child(2)
{
  color: red;
}
.popup.active .popup-inside .buttons-popup
{
  flex: 0 0 auto;
  display: flex;
  padding: 8px;
  align-items: center;
  justify-content: flex-end;
}
.popup.active .popup-inside .buttons-popup button
{
  color: inherit;
  border: 0;
  cursor: pointer;
  margin: 0;
  display: inline-flex;
  outline: 0;
  padding: 0;
  position: relative;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: transparent;
  border: 1px solid #e7e7e7;
  border-radius: 3px;
  border-radius: 0;
  vertical-align: middle;
  -moz-appearance: none;
  justify-content: center;
  text-decoration: none;
  background-color: transparent;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  padding: 6px 8px; 
  color: #1fbbb9;
  font-size: 0.875rem;
  min-width: 64px;
  box-sizing: border-box;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  line-height: 1.75;
  border-radius: 4px;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  margin-left: 10px;
}
.popup.active .popup-inside .buttons-popup button:hover {
  border-color: transparent;
  background-color: #1fbbb9;
  color: #fff;
}



