.toggle {
  position: relative;
  float: left;
  margin-right: 10px;
  display: block;
  width: 40px;
  height: 20px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0); }
  .toggle:before {
    content: "";
    position: relative;
    top: 3px;
    left: 3px;
    width: 34px;
    height: 14px;
    display: block;
    background: #9A9999;
    border-radius: 8px;
    -webkit-transition: background .2s ease;
    transition: background .2s ease; }
  .toggle span {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    display: block;
    background: white;
    border-radius: 10px;
    box-shadow: 0 3px 8px rgba(154, 153, 153, 0.5);
    -webkit-transition: all .2s ease;
    transition: all .2s ease; }
    .toggle span:before {
      content: "";
      position: absolute;
      display: block;
      margin: -18px;
      width: 56px;
      height: 56px;
      background: rgba(31, 187, 185, 0.5);
      border-radius: 50%;
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
      pointer-events: none; }

input:checked + .toggle:before {
  background: #9cf0ef; }

input:checked + .toggle span {
  background: #1fbbb9;
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
  -webkit-transition: all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25), background 0.15s ease;
  transition: all 0.2s cubic-bezier(0.8, 0.4, 0.3, 1.25), background 0.15s ease;
  box-shadow: 0 3px 8px rgba(31, 187, 185, 0.2); }
  input:checked + .toggle span:before {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
    -webkit-transition: all .4s ease;
    transition: all .4s ease; }

.center {
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 20px); }

