@font-face
{
  font-family: MontserratSemiBold;
  src: url('../fonts/Montserrat-SemiBold.ttf');
}
body
{
  overflow: hidden;
  font-family: 'Montserrat', sans-serif;
}
.auth {
  overflow: hidden;
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  background: #fff;
  transition: 0.5s linear all;
}
.loginAuth {
  position: relative;
  float: left;
  width: 0%;
  height: 100vh;
  left: -500px;
  transition: 0.5s linear all;
}
.loginAuth .login-parent { 
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
}
.loginAuth .login-parent .login-first
{
  position: relative;
  float: left;
  width: 40%;
  height: 100%;
}
.loginAuth .login-parent .login-second{
  position: relative;
  float: left;
  width: 60%;
  height: 100%;
}
.loginAuth .login-parent .login-second img
{
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.loginAuth .login-parent .login-second .overlay
{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #2d5892;
  opacity: 0.6;
  top: 0;
  left: 0;
  z-index: 9999;
}
.forgotAuth {
  position: relative;
  float: left;
  width: 0%;
  height: 100vh;
  left: -500px;
  transition: 0.5s linear all;
}
.registerAuth {
  position: relative;
  float: left;
  width: 0%;
  height: 100vh;
  right: -500px;
  transition: 0.5s linear all;
}

.registerAuth .register-parent { 
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
}

.registerAuth .register-parent .register-first
{
  position: relative;
  float: left;
  width: 40%;
  height: 100%;
}

.registerAuth .register-parent .register-second{
  position: relative;
  float: left;
  width: 60%;
  height: 100%;
  background-color: red;
}

.registerAuth .register-parent .register-second img
{
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.registerAuth .register-parent .register-second .overlay
{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #2d5892;
  opacity: 0.8;
  top: 0;
  left: 0;
  z-index: 9999;
}

.forgotAuth.active {
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  left: 0;
}
.forgotAuth.active .login
{
  width: 300px;
  top: 50%;
}
.forgotAuth.active .login .form .middle-part .loginButton button
{
  width: 100%;
}

.loginAuth.active {
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  left: 0;
}

.loginAuth.forgotActive {
  position: relative;
  float: left;
  width: 0%;
  height: 100vh;
  left: 500px;
}

.registerAuth.active {
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  right: 0;
}

.register,
.login {
  position: absolute;
  float: left;
  width: 50%;
  height: auto;
  background-color: #fff;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  transition: .3s linear all;
}

.register .form,
.login .form {
  position: relative;
  float: left;
  width: 100%;
  height: auto;
  margin-bottom: 40px;
}

.register .form input.input-control:focus,
.login .form input.input-control:focus 
{
  border-bottom: 2px solid rgba(0, 48, 119, 1);
}
.register .form input.input-control,
.login .form input.input-control {
  position: relative;
  float: left;
  width: 100%;
  height: 40px;
  margin-bottom: 20px;
  border: none;
  font-size: 15px;
  border-bottom: 2px solid rgba(0, 48, 119, 0.5); 
}
.register .form input.input-control.is-invalid,
.login .form input.input-control.is-invalid {
  border-bottom: 2px solid rgba(255, 0, 0, 1);
}
.register .form .middle-part .loginButton,
.register .form .middle-part,
.login .form .middle-part .loginButton,
.login .form .middle-part {
  position: relative;
  float: left;
  width: 100%;
}
.register .form .middle-part p {
  font-size: 15px;
}
.register .form .middle-part .loginButton
{
  display: flex;
  justify-content: center;
}
.login .form .middle-part p {
  position: relative;
  float: left;
  width: 134px;
  height: auto;
  font-family: Montserrat;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #555555;
}
.register .form .middle-part a,
.login .form .middle-part a 
{
  position: relative;
  width: 154px;
  height: 15px;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #033275;
}
.register .form .middle-part .loginButton button,
.login .form .middle-part .loginButton button 
{
  position: relative;
  float: left;
  width: 50%;
  height: 40px;
  background-color: #003077;
  border: none;
  margin-top: 20px 0px;
  color: #fff;
  border-radius: 5px;
  font-size: 18px;
}
.register .form .middle-part .loginButton button:disabled,
.login .form .middle-part .loginButton button:disabled
{
  cursor: default;
}
.register .bottom-part,
.login .bottom-part {
  position: relative;
  float: left;
  width: 100%;
  height: 50px;
  color: #555555;
}
.register .bottom-part span,
.login .bottom-part span 
{
  width: 318px;
  height: 17px;
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #555555;
  padding-top: 50px;
}
.register .bottom-part span a,
.login .bottom-part span a 
{
  color: #003077;
}

.register .user-image,
.login .user-image {
  position: relative;
  float: left;
  width: 80px;
  height: 80px;
  background-color:#033275;
  border-radius: 10px;
  margin-bottom: 20px;
}
.formlabel
{
  position: relative;
  float: left;
  width: 100%;
  height: 65px;
  border-bottom: #111;
}
.edit-info .edit-info-show .form-group label {
  width: 605px;
  height: 18px;
  font-size: 18px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 0.36px;
  text-align: left;
  color: #003275;
}
.formlabel span 
{
  width: 90px;
  height: 32px;
  font-family: Montserrat;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #555555;
}

.formlabel span .div-helper
{
  width: 30px;
  height: 2px;
  background-color: #003077;
  margin-top: 20px;
}

.register .user-image img,
.login .user-image img {
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: auto;
}

.left-menu {
  position: fixed;
  float: left;
  left: 0;
  top: 0;
  width: 70px;
  height: 100vh;
  background-color: #ffffff;
  border-right: 1px solid #f1f1f1;
  z-index: 9;
  transition: .5s linear all;
}

.left-menu.active {
  width: 200px;
  transition: .5s linear all;
}

@keyframes onLeftMenu {
  from {
    width: 70px;
  }
  to {
    width: 200px;
  }
}

/* @keyframes offLeftMenu {
  from {
    width: 200px;
  }
  to {
    width: 70px;
  }
} */

.left-menu .left-menu-buttons {
  position: absolute;
  float: left;
  width: 100%;
  height: auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.left-menu .left-menu-buttons a {
  position: relative;
  float: left;
  transition: 0.3s linear all;
  color: transparent;
  line-height: 23px;
  font-size: 0;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  z-index: 999;
}

.left-menu .left-menu-buttons a.active,
.left-menu .left-menu-buttons a:hover 
{
  box-shadow: 0px 5px 60px 0 rgba(0, 0, 0, 0.15);
  z-index: 9999;
}

.left-menu .left-menu-buttons a img {
  width: 25px;
  height: auto;
  position: relative;
  float: left;
  transition: 0.3s linear all;
  color: #003077;
}

.left-menu.active .left-menu-buttons a img {
  margin-right: 30px;
}

.left-menu.active .left-menu-buttons a {
  line-height: 23px;
  font-size: 15px;
  color: #003077;
}

.toggleLeftMenu {
  position: absolute;
  left: 50px;
  bottom: 50px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #f1f1f1;
  background-color: #003077;
  box-shadow: 0px 2px 47px -22px rgba(0,0,0,1);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999999;
  transition: .5s linear all;
}

.toggleLeftMenu.active
{
  left: 180px;
}

.toggleLeftMenu img
{
  width: 15px;
  height: 15px;
  transition: .3s linear all;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.toggleLeftMenu.active img
{
  transform: translate(-50%, -50%) rotate(-180deg);
}
.toggleLeftMenuMobile
{
  display: none;
}

.nav-bar {
  position: absolute;
  float: left;
  left: 0;
  top: 0;
  width: 100%;
  height: 70px;
  background-color: #fff;
  z-index: 10;
  border-bottom: 1px solid #f1f1f1;
}

.header-inside
{
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
}

.header-inside
{
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
}

.header-inside .logo
{
  position: relative;
  float: left;
  width: 150px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .3s linear all;
}

.header-inside .logo img
{
  position: absolute;
  float: left;
  width: 100%;
  top: 15px;
  left: 15px;
}

.header-inside .nav
{
  float: left;    
  width: calc(100% - 150px);
  margin-left: 150px;
  height: 100%;
}

.header-inside .nav .nav-wrapper
{
  float: right;
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-wrapper .dropdown
{
  position: relative;
  float: right;
  width: 100px;
  height: 100%;
}

.nav-wrapper .dropdown .profile
{
  position: relative;
  float: left;
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0px 0px 0px 20px;
  overflow: hidden;
}

.nav-wrapper .dropdown .profile img
{
  position: relative;
  float: left;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.nav-wrapper .dropdown .profile span
{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #e6e6e6;
  object-fit: cover;
  object-position: center;
  display: flex;
  justify-content: center;
  align-items: center;  
  color:#033275;
  font-size: 15px;
  font-weight: 700; 
  background-color: #f7f8fc;
}

.nav-wrapper .dropdown .button
{
  position: relative;
  float: left;
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-wrapper .dropdown .button button
{
  background-color: transparent;
  background-image: url('/../img/arrow.png');
  background-repeat: no-repeat;
  background-size: 12px;
  background-position: center;
  color:#033275;
}

.dropdown-menu.show
{
  top: -3px !important;
  left: 0px !important;
  transform: translate3d(-70px, 70px, 0px) !important;
}

.nav-wrapper a
{
  position: relative;
  float: left;
  width: auto;
  height: 100%;
  margin: 0px 20px;
  display: flex;
  align-items: center;
}

.nav-wrapper a .img
{
  position: relative;
  float: left;
  width: auto;
  height: 25px;
}

.nav-wrapper .search
{
  position: relative;
  float: left;
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0px 20px;
}

.nav-wrapper .search input
{
  opacity: 0.5;
  border-radius: 3px;
  border: solid 1px #d6d6d6;
  background-color: #d6d6d6;
}

