.content {
  position: relative;
  float: left;
  width: 100%;
  height: 100vh;
  /* background-color: black; */
}

.first-chart {
  position: relative;
  float: left;
  width: calc(70% - 10px);
  margin-right: 10px;
  height: calc(50% - 20px);
  margin-bottom: 20px;
  border: solid 1px #f1f1f1;
  background-color: #ffffff;
}

.chart {
  animation: showCharts 1.7s;
  border: 1px solid #eeeeee;
}

.chart.hide {
  animation: hideCharts 1.7s;
}
@keyframes hideCharts {
  from {
    opacity: 1;
  }
  to {
    transition: 0.5s linear all;
    opacity: 0;
  }
}

@keyframes showCharts {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.favorites-section {
  position: relative;
  float: left;
  width: calc(30% - 10px);
  margin-right: 10px;
  height: calc(50% - 20px);
  margin-bottom: 20px;
  border: 1px solid #eeeeee;
}

.favorites-section.active {
  width: 100%;
  height: 100%;
  transition: 0.8s linear all;
  animation: onFavorites 0.8s backwards;
}

@keyframes onFavorites {
  from {
    width: calc(30% - 10px);
    height: calc(50% - 20px);
  }
  to {
    width: 100%;
    height: 100%;
  }
}
